$text: #FF2D00;
$size: 14pt;
$border: 1px #000 solid;
$highlight: #000;

$background-color: #323232;

$max_cols : 12;
$max_width : 100%;
$single_col : $max_width / $max_cols;

@for $i from 1 through 12 {
    .col-#{$i} { width: $single_col * $i }
}

*,
*:after,
*:before {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; 
}

.wrapper {
    width: 100%;
    margin: 0 auto;
}

img {
    width: auto;
    max-width: 100%;
    height: auto !important; 
}

.wrapper:after,
.row:after {
      content: "";
      display: table;
      clear: both; 
}

[class*='col-'] {
    float: left;
    min-height: 1px;
}

[class*='col-']:last-of-type {
    padding-right: 0;
}

p {
    color: $text;
    font-size: $size;
    &:hover{color: $highlight; text-decoration: underline;}
} 

.nav {
    margin: 0 auto;
    border: $border;
    height: 30px;
    background-color: $background-color;
    
    ul {
        margin: 0 auto;
        height: 80px;
        display: inline;
    }
    
    li {
        padding: 5px;
        float: left;
        width: 60px;
        list-style: none;
        margin-left: 5px;
    }
}

